


.Navigation-Wrapper{
    width:19vw;
    background-color: black;
    font-family: "Press Start 2P",sans-serif;
 
}
.Navigation-Wrapper select{
    cursor: pointer;
    font-family: "Press Start 2P",sans-serif;
    font-size: clamp(4px,1vw,9px);
    width:100%;
    background-color: black;
    color: white;
    height: 7vh;
}
.Navigation-Wrapper select option{
     height: 1vh;
     cursor: pointer;
}
#Unwanted{
    display: none;
}
.Saloon-Wrapper{ 
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.Saloon-Wrapper h6{
    
    font-size: clamp(6px,2vw,14px)
}
.Saloon-Wrapper button{
    background-color: transparent;
    padding:10px;  
    line-height: 1.2;
    border:none ;
    font-family: "Press Start 2P",sans-serif;
    cursor: pointer;
    width: 99%;
    font-size: 8px;
    text-transform: uppercase;
    font-weight: bold;
    color: #00c800;
    border:2px solid #00ff00; 
    animation: neon-pulse 2s ease-in-out infinite alternate;    
    
}
@media only screen and (max-width:550px){
    .Saloon-Wrapper button{
        font-size: clamp(3px,1vw,9px)
      
    }
}