


.User-Wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content:center;
    align-items: center;
    margin: 0;
    padding: 0;
    font-family: "Press Start 2P",sans-serif;

}
.User-Image-Wrapper{
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center; 
    width: 100%;
    justify-content: flex-start;
}


.User-Head{
    display: flex;
    font-family: "Press Start 2P",sans-serif;    
    text-transform: uppercase;
    max-height: 6vh;
}
.User-Head span{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size:  clamp(10px, 1.2vw, 20px);
    color:gold;

    letter-spacing: 0.1px;
    width: 100%;
    
}
#CowboyHat{
    width: 40%;
    transform: translate(0%,+10%);
}

.User-Image-Wrapper img{
    min-width: 8vw;
    max-width: 8vw;
    min-height: 9vw;
    max-height: 9vw;
    border:2px solid #00ff00; 
    animation: neon-pulse 2s ease-in-out infinite alternate;   
}
.User-Image-Wrapper button{
    background-color: transparent;
    height:clamp(5px, 1vw, 22px); ; 
    border:none ;
    font-family: "Press Start 2P",sans-serif;
    cursor: pointer;
    font-weight: bold;
    border:2px solid #00ff00; 
    animation: neon-pulse 2s ease-in-out infinite alternate;    
    width:  clamp(5px, 1vw, 22px);
    margin-top: -20px;
    
  }
