

.UserHome-Wrapper{
margin: 0;
padding: 0;
width: 100%;
display: flex;
}
.UserHome_Navigation-Wrapper{
    max-width: 14vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px; 
    background-color: black;
    
}
.Navigation-Wrapper_User-Wrapper{
    max-height: 15vw;
}
.UserHome-Navigation{ display:flex; flex-direction: column;gap: 5px; }
.UserHome-Navigation button{
    cursor: pointer;
    display: flex;
    gap:5px;
    justify-content: flex-start;
    align-items: center;
background-color: transparent;
 border:2px solid #00ff00; 
 animation: neon-pulse 2s ease-in-out infinite alternate;   
 width: 8vw;
}
.UserHome-Navigation button p{
   
    font-family: "Press Start 2P",sans-serif;
    font-size:  clamp(3px,0.6vw, 15px);
    color: #00ff00e3; 
    letter-spacing: -0.1em;
}
.UserHome-Navigation img{
    width:1.8vw;
    
}


#userHomeLoading{
    margin-top: 0px;
}
.UserHome_Display-Wrapper{
    font-family: "Press Start 2P",sans-serif;    
    color: #00c800;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size:  clamp(3px,2vw, 20px);
}